.zolo-tabs-container {
    .zolo-tabs {
        align-items: center;
        display: flex;
        justify-content: space-between;
        margin-bottom: 16px;
        background: #f2f2f2;
        border-radius: 6px;
        .components-button {
            align-items: center;
            color: #555;
            cursor: pointer;
            display: inline-flex;
            flex-grow: 1;
            justify-content: center;
            padding: 2px;
            transition: all 0.3s ease-in-out;
            box-shadow: none;
            height: 34px;
            font-size: 12px;
            margin: 3px;
            border-radius: 6px;
            border: 1px solid #f2f2f2;
            &.is-primary {
                background-color: #fff;
                border-color: #f2f2f2;
                color: #001feb;
            }
            &:hover {
                box-shadow: none;
            }
        }
    }
    .zolo-tab-content {
        margin-bottom: 16px;
    }
}
