.zolo-icons-wrap {
    padding: 0 30px 30px 30px;
    overflow-y: scroll;
    height: 475px;
    min-height: 475px;
    /* width */
    &::-webkit-scrollbar {
        width: 5px;
        border-radius: 10px;
    }
    /* Track */
    &::-webkit-scrollbar-track {
        background: #f1f1f1;
        border-radius: 10px;
    }
    /* Handle */
    &::-webkit-scrollbar-thumb {
        background: #aeaeae;
    }
    /* Handle on hover */
    &::-webkit-scrollbar-thumb:hover {
        background: #555;
    }
    // Display Icon
    .zolo__display-icon {
        display: inline;
        svg {
            display: block;
            width: 1em;
            height: 1em;
        }
    }
}

.zolo__icons-grid {
    display: grid;
    gap: 15px;
    grid-template-columns: repeat(7, 1fr);
    position: relative;

    .single__icon {
        height: 70px;
        border: 1px solid #ececf1;
        border-radius: 4px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        margin: 0;
        padding: 0 !important;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
        transition: border 0.3s ease;
        &:hover {
            border-color: #dbdbdd;
            svg {
                color: #001feb;
            }
        }
        svg {
            width: 24px;
            height: 24px;
            color: #5d5b65;
        }
        &.active {
            border-color: #b9befd;
            border-width: 2px;
            svg {
                color: #001feb;
            }
        }
    }
}

.zolo-icons-title {
    margin: 0 0 20px;
}

.zolo__modal.components-modal__frame {
    width: 1024px;
    min-width: 1024px;
    height: auto;
    min-height: auto;
    .components-modal__header {
        border-bottom: 1px solid #ececf1;
        padding: 25px 30px;
    }

    .components-modal__content {
        padding: 0;
    }

    .components-modal__content {
        overflow: hidden;
    }

    .modal__content {
        width: calc(100% - 180px);
        box-sizing: border-box;
        box-sizing: border-box;
    }

    .category__button {
        padding: 20px 30px;
        width: 100%;
        height: auto;
        font-size: 14px;
        text-align: left;
        border-radius: 0 !important;
        display: flex;
        align-items: center;
        gap: 8px;

        &:nth-last-child(1) {
            border-bottom: 1px solid #ececf1;
        }
        svg {
            display: block;
            fill: none;
        }
        &:hover {
            color: #001feb;
        }

        &:focus {
            outline: none !important;
            box-shadow: none !important;
        }
    }

    .zolo-custom-icon {
        padding: 14px;
        margin: 0;
        text-align: center;
        color: #34363b;
        background-color: #d7e7fb;
        margin: 24px;
        border-radius: 4px;
        font-size: 13px;
        color: #2f2f3f;
        strong {
            color: #001feb;
        }
    }

    .category__button.active {
        background: #e9e8f3;
        border-radius: 0 !important;
        color: #001feb;
    }

    .search__input {
        border: 1px solid #ececf1;
        border-radius: 4px;
        display: flex;
        align-items: center;
        margin: 30px;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
        input {
            border: none;
            width: calc(100% - 40px);
            padding: 10px 14px;
            &:focus {
                outline: none !important;
                box-shadow: none !important;
            }
        }

        svg {
            width: 14px;
            height: 14px;
            margin: 0 15px;
        }
    }
}

.zolo-icon-preview {
    margin-bottom: 16px;
    label {
        display: block;
        margin: 0 0 8px;
    }
}

