    width: 280px;
    min-width: 280px;
    height: 585px;
    min-height: 585px;
    border-right: 1px solid #ececf1;
    padding-top: 30px;
}
.zolo-icon-picker {
    .zolo__icon-preview-wrap {
        position: relative;
        overflow: hidden;
        .zolo__icon-preview {
            --zolo-bg-default: #fff;
            --zolo-border-color-bold: #d5d8dc;
            --zolo-border-bold: 1px solid var(--zolo-border-color-bold);
            border-radius: 6px;
            height: 130px;
            cursor: pointer;
            position: relative;
            background-color: var(--zolo-bg-default);
            background-image: linear-gradient(
                    45deg,
                    var(--zolo-border-color-bold) 25%,
                    transparent 0,
                    transparent 75%,
                    var(--zolo-border-color-bold) 0,
                    var(--zolo-border-color-bold)
                ),
                linear-gradient(
                    45deg,
                    var(--zolo-border-color-bold) 25%,
                    transparent 0,
                    transparent 75%,
                    var(--zolo-border-color-bold) 0,
                    var(--zolo-border-color-bold)
                );
            background-position:
                0 0,
                calc(16px / 2) calc(16px / 2);
            background-size: 16px 16px;
            border: var(--e-a-border-bold);

            &::after {
                background-color: rgba(0, 0, 0, 0.2);
                content: '';
                inset: 0;
                pointer-events: none;
                position: absolute;
                border-radius: inherit;
                opacity: 0;
                transition: opacity 0.2s ease;
            }
        }

        &:hover {
            .zolo__icon-preview {
                &::after {
                    opacity: 1;
                }
            }

            .zolo__icon-picker-buttons {
                .zolo-picker__button {
                    transform: translateY(0);
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
    }
    .zolo__icon-picker-buttons {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        display: flex;
        align-items: center;
        gap: 1px;
        z-index: 99;
        .zolo-picker__button {
            font-size: 12px;
            background: var(--zoloblocks-brand-color);
            width: 100%;
            box-sizing: border-box;
            color: #fff;
            border-radius: unset;
            height: 30px;
            justify-content: center;
            transform: translateY(10px);
            opacity: 0;
            visibility: hidden;
            transition:
                transform 0.3s,
                opacity 0.3s,
                visibility 0.3s,
                background 0.3s;
            &:hover {
                background: var(--zoloblocks-brand-color-hover);
            }
        }
    }
    .zolo__single-preview-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        svg {
            width: 50px;
            height: 50px;
            fill: #4c4a53;
            display: block;
        }
    }
}
